<template>
  <div>
    <van-field
      v-bind="$attrs"
      :value="value | dateFilter"
      readonly
      clickable
      :disabled="disabled"
      :right-icon="rightIcon"
      @click="onClick"
      @click-right-icon.stop="onRightIconClick"
    />
    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="date"
        type="date"
        title="选择日期"
        @confirm="onConfirm"
        @cancel="show = false"
      />
    </van-popup>
  </div>
</template>

<script>
import dayjs from 'dayjs';

export default {
  name: 'VantDatePicker',
  components: {},
  filters: {
    dateFilter(date) {
      return date && dayjs(date).format('YYYY-MM-DD');
    },
  },
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    value: {
      type: [Date, String, Number],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    valueFormat: {
      type: String,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      show: false,
      date: new Date(),
    };
  },
  computed: {
    rightIcon() {
      if (this.value && this.clearable) {
        return 'clear';
      }
      return this.show ? 'arrow-up' : 'arrow-down';
    },
  },
  methods: {
    onClick() {
      this.date = this.value || new Date();
      this.show = !this.disabled;
    },
    onConfirm(time) {
      if (this.valueFormat) {
        if (this.valueFormat === 'timestamp') {
          this.$emit('change', dayjs(time).valueOf());
        } else {
          this.$emit('change', dayjs(time).format(this.valueFormat));
        }
      } else {
        this.$emit('change', time);
      }
      this.show = false;
    },
    onRightIconClick() {
      if (this.clearable && this.value) {
        this.$emit('change', undefined);
      } else {
        this.onClick();
      }
    },
  },
  created() {},
};
</script>

<style lang="scss" scoped>
</style>
